.footer-logo{
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem 0 2rem 0;
    border-top: 2px solid #d8d6d6;
    margin-top: 4rem;

	.footer-img{
		width: 4rem;
		margin-bottom: 1rem;
		@media (max-width: 700px) {
			width: 2rem;
		}
	}
	p{
		font-size: 1rem;
		text-align: center;
		@media (max-width: 700px) {
			font-size: 0.8rem;
		}
	}
}